<template>
  <div id="app">
    <!-- 头部区域 -->
    <header id="home-header">
      <router-link to="">
        <!-- <img src="../assets/imgs/logo.png" alt="" id="logo"> -->
      </router-link>

      <!-- 登陆人员信息 -->
      <div id="userInfo">
        <el-dropdown :hide-on-click="false" id="home-dropdown">
          <span class="el-dropdown-link">
            <i class="fa fa-user"></i>
            <span>{{userInfo.xm}}</span>
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="logout">注销登录</el-dropdown-item>
            <el-dropdown-item @click.native="updatePwd">修改密码</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>

    </header>

    <!-- 中间区域 -->
    <div id="mainframe" v-loading="loadingFullScreen" element-loading-text="请稍等片刻">


      <!-- 菜单 -->
      <div id="menu">
        <el-row class="tac">
          <el-col :span="12" v-bind:class="[isCollapse ? collapsed:'',noCollapse]">
            <el-menu
            background-color="#545c64" text-color="#fff" active-text-color="#ffd04b"  
            :default-active="$route.path" :unique-opened="uniqueOpened" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse" router>
              <el-submenu :index="item.code" v-for="item in UserMenuList" :key="item.code">
                <template slot="title">
                  <i :class="item.icon" style="color:#fff;"></i> &nbsp;
                  <span>{{item.title}}</span>
                </template>
                  <el-menu-item :index="'/'+subItem.href" v-for="subItem in item.children" :key="subItem.code"> 
                      <i :class="subItem.icon" style="color:#fff;position:relative;top:-2px;left:-6px;"></i>{{subItem.title}}
                  </el-menu-item>
              </el-submenu>

            </el-menu>
          </el-col>
        </el-row>
        <div class="bottonMenu" v-bind:style="{width:bottomMemuWidth}">
          <i class="el-icon-menu" id="clomenu" @click='collapsemenu'></i>
        </div>
      </div>

      <!-- 右边主要操作区域 -->
      <el-main id="main">
        <router-view id="mainView"/>
      </el-main>
      
      
      <!-- 修改密码弹出层 -->
      <el-dialog title="修改密码" :visible.sync="dialogTableUpdatePwd" width="40%">
        <el-form :model="editData">       
          <el-row>
            <el-col :span="24">
              <div class="grid-content">
                <el-form-item label="旧密码" :label-width="formLabelWidth" width="87">
                  <el-input v-model="editData.oldPwd" auto-complete="off" type="password" placeholder="请输入旧密码"></el-input>
                </el-form-item>
              </div>
            </el-col>
            <el-col :span="24">
              <div class="grid-content">
                <el-form-item label="新密码" :label-width="formLabelWidth">
                  <el-input v-model="editData.newPwd" auto-complete="off" placeholder="请输入8位以上含字母、数字、特殊字符中的两项组合！" type="password"></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <div class="grid-content">
                <el-form-item label="确认密码" :label-width="formLabelWidth" width="87">
                  <el-input v-model="editData.rePwd" auto-complete="off" type="password" placeholder="请输入确认密码"></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
          <p class="tips">*请输入8位以上含字母、数字、特殊字符中的两项组合</p>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancelThis2">取 消</el-button>
          <el-button type="primary" @click="saveUpdatePwd">确 定</el-button>
        </div>
      </el-dialog>

    <!-- 底部 -->
    <footer id="home-footer"> copyright©2017-2027 qsbpdata.com All Rights Reserved 粤ICP备17007967号</footer>
  </div>
</div>
</template>

<script>
import Axios from 'axios'
import { setTimeout, setInterval, clearInterval } from 'timers';
export default {
  name: 'App',
  data() {
    return {
      dialogTableUpdatePwd:false,
	    uniqueOpened:true,
      isCollapse: false,
      collapsed:"collapsed",
      noCollapse:"noCollapse",
      userInfo:{},
      UserMenuList:[],
      bottomMemuWidth:'200px',
      keyValue:'',
      loadingFullScreen: false,
      loading:false,
       editData:{
        oldPwd:'',
        newPwd:'',
        rePwd:''
      },
      formLabelWidth: '82px',
    };
  },
  provide(){
    return{
    }
  },
  methods:{
    //点击修改密码
    updatePwd(){
      this.dialogTableUpdatePwd = true;
    },
    //保存修改密码
    saveUpdatePwd(){
      if ((this.editData.oldPwd == null ||  this.editData.oldPwd== "")
			&& (this.editData.newPwd == null || this.editData.newPwd == "")
			&& (this.editData.rePwd == null || this.editData.rePwd == "")) {
        this.$message({
          type: 'info',
          message: '表单不能为空！'
        }); 
        return;        
      }
      if( !/^(?![A-Z]+$)(?![a-z]+$)(?!\d+$)(?![\W_]+$)\S{6,30}$/.test(this.editData.newPwd) ){
        this.$message({
          type: 'info',
          message: '请输入6位以上含字母、数字、特殊字符中的两项组合！'
        });
        return;
      }
      if(this.editData.oldPwd==this.editData.newPwd){
        this.$message({
          type: 'info',
          message: '新密码不能与旧密码一致！'
        });
        return;
      }
      if (this.editData.newPwd != this.editData.rePwd) {
        this.$message({
          type: 'info',
          message: '两次密码不一致,请重新输入！'
        });
        return;
      } 
      this.loading=true;
      var that=this;
      Axios({
        method:'POST',
        url:this.projectName+'user/updatePassword',
        params:{
          "oldPassword":that.editData.oldPwd,
          "newPassword":that.editData.newPwd
        },
        headers:{
            'Authorization':sessionStorage.getItem("tokenHead")+sessionStorage.getItem("token")
        },
        }).then(function (response) {
          console.log(response.data)
          if( response.data.code==200 ){
            that.$alert(response.data.message+' 即将跳转登录页', '提示', {
              confirmButtonText: '确定',
              callback: action => {
                 that.dialogTableUpdatePwd = false;           
                  that.editData={
                    oldPwd:'',
                    newPwd:'',
                    rePwd:''
                  }
                  that.$router.push("login");
              }
            });
           
          }else{
            that.$message({
              type: 'info',
              message: response.data.message
            });
          }
          that.loading=false;
        })
      .catch(function (error) {
        that.loading=false;
      })
    },
    cancelThis2(){
      this.dialogTableUpdatePwd=false;
    },
    // 菜单展开关闭事件
    handleOpen(key, keyPath) {
    },
    handleClose(key, keyPath) {
    },
    collapsemenu(){
      this.isCollapse=!this.isCollapse;
      if(this.isCollapse){
        this.bottomMemuWidth="64px";
      }else{
        this.bottomMemuWidth="200px";
      }
    },
    // 查询菜单 兼职 登录身份判断
    queryMenu(){
      var that=this;
      Axios({
        method:'POST',
        url:this.projectName+"menu/queryMenu",
        params:{
        },
        headers:{
            'Authorization':sessionStorage.getItem("tokenHead")+sessionStorage.getItem("token")
        }
        }).then(function (response) {
          if (response.data.code == 200) {
              that.UserMenuList=response.data.data;
              console.log(that.UserMenuList)
          }
      })
      .catch(function (error) {
      });
    },
    // 注销登录
    logout(){
      var that=this;
      this.$confirm('确定注销登录?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        sessionStorage.removeItem("tokenHead");
        sessionStorage.removeItem("token");
        sessionStorage.removeItem("userInfo");        
        localStorage.clear();
        sessionStorage.clear();
        that.$router.push('login');
        // Axios({
        //   methods:'post',
        //   url:this.projectName+'LoginServlet',
        //   params:{
        //       action:'loginOut'
        //   }
        //   }).then(function (response) {
        //     console.log(response.data);
        //   if(response.data.result=="Success"){
        //     sessionStorage.clear();
        //     that.$router.push('login');
        //   }
        // })
        // .catch(function (error) {
          
        // })
      }).catch(() => {
    
      })
    },
  },
  created:function(){
    this.queryMenu();
    var userInfo={};

    if(userInfo){
      userInfo=JSON.parse(sessionStorage.getItem("userInfo"));
    }else{
      userInfo={};
    }
    this.userInfo=userInfo;
    console.log(this.userInfo);
  },
  mounted:function(){    
  }
}
</script>

<style lang="scss">
*{
  margin: 0;
  padding: 0;
}
body,html,#app{
  width: 100%;
  height: 100%;
}
// #tab-first{
//   padding-left: 4px;
// }
.el-tabs__active-bar .is-top{
  width: 40px!important;
}
.seeMore{
  text-decoration: underline;
  color: #7171B7;
  cursor: pointer;
}
.el-table--border th,
.el-table th{
  background: aliceblue;
}
.el-menu-item{
  a{
    text-decoration: none;
  }
}
#menu{
  .el-col{
    transition: all .4s;
  }
  .el-menu{
    border: none;
  }
  .noCollapse{
    width: 201px;
  }
  .collapsed{
    width: 64px;
  }
}
#mesDrop{
  height: 100%;
  width: 100%;
}
#home-header{
  width: 100%;
  height: 50px;
  // background:#23262E;
  background-image: radial-gradient(circle,#99CCCC, #7171B7);
  // border-bottom: 3px solid #23262E;
  // background: rgb(82, 195, 221);
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 1;
  #logo{
    width: 90px;
    float: left;
    margin: 12px 0px 0px 15px;
  }
  #zyck-container{
    float: right;
    height: 49px;
    line-height: 49px;
    color: #fff;
    font-size: 14px;
    margin-right: 40px;
    cursor: pointer;
    .littleIcon{
      width: 20px;
      position: relative;
      top: 5px;
      margin-right: 3px;
    }
  }
  #message-push-container{
    float: right;
    height: 49px;
    line-height: 49px;
    color: #fff;
    margin-right: 50px;
    i{
      font-size: 20px;
      cursor: pointer;
      animation: shining 1.5s infinite alternate;
    }
    @keyframes shining {
      0%{
        opacity: 0.2;
      }
      100%{
        opacity: 1;
      }
    }
  }
  #message-container{
    float: right;
    height: 49px;
    line-height: 49px;
    color: #fff;
    font-size: 14px;
    margin-right: 40px;
    cursor: pointer;
    padding: 0px 0px;
    position: relative;
    width: 30px;
    .message{
      width: 28px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
    .messageBadge{
      display: block;
      text-align: center;
      line-height: 14px;
      font-size: 10px;
      color: #fff;
      border-radius: 100%;
      margin: 0 auto;
      position: absolute;
      top: 4px;
      right: -6px;
      padding: 1px 3px;
    }
  }
  #userInfo{
    float: right;
    right: 0px;
    height: 49px;
    padding-right: 20px;
    #home-dropdown{
      height: 49px;
      line-height: 49px;
      color: #fff;
      cursor: pointer;
    }
    .userimg{
      position: relative;
      width: 20px;
      top: 4px;
      left: -3px;
    }
    .el-icon--right{
      margin-left: 8px;
    }
  }
}
#mainframe{
  display: -webkit-flex;
  display: flex;
  display: -ms-flex;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding-top: 50px;
  .tips{
    color:red;
    font-size: 12px;
    text-indent: 10px;
  }
  #menu{
    // background: #fff;
    background: #545c64;
    height: 100%;
    border-right: 1px solid #e5e5e5;
    overflow-x: hidden;
    text-align: left;
    box-sizing: border-box;
    padding-bottom: 90px;
    position: relative;
    z-index: 2;
    a{
      width: 100%;
      height: 50px;
      background: none;
      text-decoration: none;
      display: block;
      color: #000;
    }
    .el-submenu{
      width: 100%;
    }
    .bottonMenu{
      height: 50px;
      position: fixed;
      bottom: 40px;
      transition: all 0.4s;
      // background: #fff;
      background: #545c64;
      #clomenu{
        cursor: pointer;
        font-size: 20px;
        // color: #666;
        color: #fff;
        position: absolute;
        bottom: 13px;
        z-index: 99;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }
  #main{
    flex-grow: 1;
    width: 0;
    text-align: left;
    margin: 0;
    padding: 0px 0px 40px 0px;
    background: #f2f2f2;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
  }
}
#home-footer{
  width: 100%;
  position: absolute;
  bottom: 0px;
  height: 39px;
  line-height: 39px;
  background: #fff;
  text-align: center;
  font-size: 11px;
  border-top: 1px solid #e5e5e5;
  z-index: 3;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  width: 100%;
  height: 100%;
}
#zyck_qrcode{
  position: fixed;
  top: 0px;
  right: 0px;
  left: 0px;
  bottom: 0px;
  background: rgba(0,0,0,0.3);
  z-index: 999;
  .qrcode_container{
    width: 800px;
    height: 470px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    #closeTemplate{
      position: absolute;
      top: -30px;
      right: -30px;
      color: #fff;
      font-size: 20px;
      cursor: pointer;
    }
    #qrcode_template_one,#qrcode_template_Two{
      width: 360px;
      height: 470px;
      background: #fff;
      float: left;
      .bgImg{
        width: 100%;
        height: 470px;
      }
      .el-icon-download{
        font-size: 28px;
        color: #fff;
        margin-top: 10px;
        cursor: pointer;
        left: 50%;
        transform: translateX(-50%);
      }
      .codeBox{
        width: 210px;
        height: 210px;
        background: #fff;
        position: absolute;
        top: 113px;
        left: 74px;
        .littleIcon{
          width: 30px;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translateX(-50%) translateY(-50%);
          z-index:22;
        }
      }
    }
    #qrcode_template_one{
      margin-right: 80px;
      .codeBox{
        width: 180px;
        height: 180px;
        background: #fff;
        position: absolute;
        top: 197px;
        left: 94px;
      }
    }
  }
}
</style>



